import React from "react";
import "./styles.css";
import { productImg } from "./assets/product-img.webp";

const ProductCard = () => {
  return (
    <div className="card">
      <img src={productImg} />
      <div className="card-img-top">
        <img src={productImg} />
      </div>
    </div>
  );
};

export default ProductCard;
